* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  touch-action: none;
}

body {
  -webkit-user-select: none;
  user-select: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  display: flex;
}

h2 {
  margin-top: 10px;
}

.card {
  background-color: #f0f0f0;
  border: 4px solid #272727;
  border-radius: 10px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-width: 300px;
  min-height: 200px;
  font-size: 24px;
  display: flex;
  box-shadow: 0 4px 8px #0003;

  & ul, & li, & button {
    font-size: large;
    font-weight: 900;
  }

  & .image {
    width: 60px;
    margin-top: 2px;
    margin-bottom: 2px;
  }

  & button, & input {
    text-align: center;
    border: none;
    border-radius: 5px;
    width: 70%;
    margin: 3px;
    padding: 10px;
    font-size: 15px;
  }

  & button {
    background-color: #ae7a83;
    transition: background-color .35s;
  }

  & button:active {
    background-color: #7aae97;
    transition: background-color;
  }

  & ul {
    color: #fdeaee;
    flex-wrap: wrap;
    width: calc(70% + 10px);
    margin-bottom: 15px;
    font-size: 15px;
    list-style: none;
    display: flex;
  }

  & li {
    text-align: center;
    background-color: #cd5569;
    border: 2px solid #962020;
    border-radius: 5px;
    flex-grow: 1;
    margin: 5px;
    padding: 5px;
    box-shadow: 1px 2px 5px #000000a4;
  }
}

.sign {
  width: 70%;
  display: flex;
}

.initial-login {
  flex-direction: column;
  align-items: center;
  gap: 20px;
  display: flex;
}

.initial-login .card {
  padding: 20px;
}

.initial-login button {
  margin-top: 10px;
}
